// Font Import
// --------------------------------------------------
@font-face {
  font-family: "Helvetica", sans-serif;
  src: local("Helvetica Regular"), local("helvetica_regular"),
    url("../fonts/Helvetica/helvetica_regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Helvetica", sans-serif;
  src: local("Helvetica Bold"), local("helvetica_bold"),
    url("../fonts/Helvetica/helvetica_bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "Proba Pro";
  src: local("ProbaPro Bold"), local("Proba-Pro-Bold"),
    url("../fonts/Proba/ProbaPro-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "icomoon";
  src: url("../fonts/icon-font/icomoon.ttf?lt38un") format("truetype"),
    url("../fonts/icon-font/icomoon.woff?lt38un") format("woff"),
    url("../fonts/icon-font/icomoon.svg?lt38un#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

