.product-card-wrapper {
  background-color: $grey-light-clr;
  .product-card__row {
    margin: 0 -20px;
  }
  .product-card__left,
  .product-card__right {
    padding: 0 20px;
    margin-bottom: 145px;
  }
  .product-card__left {
    flex: 0 0 692px;
    max-width: 692px;
  }
  .product-card__right {
    flex: 0 0 calc(100% - 692px);
  }
}

.product-card__title {
  font-size: 48px;
  line-height: 56px;
}

.product-slider-wrapper {
  .product-slider {
    margin-bottom: 27px;
  }
  .product-slider__item {
    position: relative;
    border-radius: 4px;
    background-color: $white-clr;
    img {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      height: 100%;
      width: 100%;
      max-width: 80%;
      object-fit: contain;
      margin: 0 auto;
    }
    &::before {
      content: "";
      display: block;
      padding-bottom: 50%;
    }
    &::after {
      content: "";
      display: block;
      padding-top: 50%;
    }
  }
  .product-slider__thumbs {
    padding-right: 5px;
    .slick-list {
      margin: 0 -13.5px;
    }
  }
  .product-slider__thumbs-item {
    background-color: $white-clr;
    margin: 0 13.5px;
    border-radius: 4px;
    border: 1px solid transparent;
    cursor: pointer;
    position: relative;
    width: 86px;
    height: 86px;
    display: flex;
    align-items: center;
    overflow: hidden;
    outline: none !important;
    box-shadow: none !important;
    @media (max-width: 600px) {
      height: 38px;
    }
    img {
      object-fit: contain;
      width: 86px;
      height: 86px;
      margin: 0 auto;
      @media (max-width: 600px) {
        width: 38px;
        height: 38px;
      }
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 1px solid $accent-clr;
      border-radius: 4px;
      opacity: 0;
      visibility: hidden;
      transition: 0.35s;
    }
    &.slick-current {
      &::before {
        opacity: 1;
        visibility: visible;
      }
    }
    @include hover {
      &::before {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

.product-card__title {
  font-family: $font-family-proba;
  max-width: 483px;
  line-height: 56px;
  margin-bottom: 5px;
  span {
    color: $accent-clr;
  }
}

.product-card__about {
  background: $white-clr;
  box-shadow: 5px 9px 30px rgba(175, 175, 175, 0.12);
  border-radius: 4px;
  .product-card__about-header {
    padding: 36px 30px 0;
  }
  .product-card__id {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    .product-availability {
      flex: 1;
      margin-bottom: 28px;
    }
    .id-text {
      margin-bottom: 28px;
      margin-right: 14px;
      color: $grey-lighter-clr;
    }
  }
  .select2 {
    display: block;
    width: 100% !important;
    border-radius: 8px;
    background-color: $white-clr;
    * {
      outline: none !important;
      box-shadow: none !important;
    }
    .select2-results__option--selectable {
      display: flex;
    }
  }

  .product-card__selects {
    padding: 0 32px 17px;
    .select2-container.select2-container--open {
      .select2-selection--single {
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
        .select2-selection__rendered {
          .card-select-list {
            &::after {
              transform: translateY(-50%) rotate(180deg);
            }
          }
        }
      }
    }
    .select2-container {
      .select2-selection--single {
        height: 56px;
        margin-bottom: 24px;
        border: 1px solid $grey-superlight-clr;
        border-radius: 8px;
        .select2-selection__rendered {
          padding: 0 16px;
          height: 100%;
          .card-select-list {
            font-weight: bold;
            display: flex;
            align-items: center;
            height: 100%;
            list-style: none;
            padding-left: 0;
            margin-bottom: 0;
            position: relative;
            &::after {
              content: "\e912";
              font-family: "icomoon";
              position: absolute;
              right: 0;
              top: 50%;
              transform: translateY(-50%) rotate(0);
              width: 13px;
              height: 8px;
              font-size: 8px;
              font-weight: normal;
              color: $grey-lighter-clr;
              display: block;
              transition: transform 0.25s;
            }
            .state-text {
              text-overflow: ellipsis;
              overflow: hidden;
              width: 200px;
              white-space: nowrap;
            }
            .state-title {
              font-weight: 400;
              flex: 0 0 auto;
              padding-right: 20px;
              min-width: 120px;
            }
            i {
              color: $grey-lighter-clr;
              font-size: 8px;
              margin-left: auto;
            }
            img {
              margin-right: 8px;
            }
          }
        }
      }
    }
    .select2-selection__arrow {
      display: none;
    }
  }
}

.state-img-wrapper {
  flex: 0 0 auto;
  display: flex;
  align-items: center;
}

.select2-results {
  .card-select-list__type {
    .state-img-wrapper {
      width: 100%;
      @media (max-width: 575px) {
        display: grid;
        grid-template-columns: 38px 1fr;
        column-gap: 17px;
        align-items: center;
        grid-template-areas:
          "img-flag state-text"
          "img-flag stock";
        .state-text,
        .product-availability__stock {
          grid-column: 2 / 2;
          line-height: 27px;
        }
        .state-text {
          grid-area: state-text;
        }
        .product-availability__stock {
          grid-area: stock;
        }
        .img-flag {
          grid-column: 1 / 2;
          grid-area: img-flag;
        }
      }
    }
  }
}

.select2-container--select-dropdown {
  .select2-dropdown {
    border-color: $grey-superlight-clr;
    border-radius: 8px;
    border-top-right-radius: 0;
    border-top-left-radius: 0;
    // .select2-results {
    // 	// box-shadow: 5px 9px 30px rgba(69, 69, 69, 0.12);
    // }
    .select2-results__option--selectable {
      padding: 8px 16px;
      border-bottom: 1px solid $grey-superlight-clr;
      min-height: 55px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      &:last-of-type {
        border-bottom: none;
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
      }
      // &.select2-results__option--selected {
      // 	display: none;
      // }
      @include hover {
        background-color: $accent-clr-lighter;
      }
      .card-select-list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        list-style: none;
        padding-left: 0;
        width: 100%;
        .state-text {
          text-overflow: ellipsis;
          overflow: hidden;
          width: 300px;
          white-space: nowrap;
          display: inline-block;
          @media (max-width: 1230px) {
            width: 200px;
          }
        }
        .img-flag {
          margin-right: 15px;
        }
        .product-availability__status {
          display: flex;
          align-items: center;
          &.product-availability__stock {
            color: $accent-clr;
            margin-left: auto;
            display: inline-block;
            @media (max-width: 575px) {
              margin-left: 0;
              display: block;
            }
          }
        }
      }
    }
  }
}

.dropup .dropdown-menu {
  bottom: auto !important;
}

.img-flag {
  width: 38px;
  height: 38px;
}

.product-card__description {
  padding: 23px 32px 18px 35px;
  border-top: 1px solid $grey-superlight-clr;
  border-bottom: 1px solid $grey-superlight-clr;
  .product-description__header {
    .btn {
      width: 100%;
      display: flex;
      justify-content: space-between;
      padding: 0;
      font-size: 18px;
      line-height: 27px;
      letter-spacing: 0;
      text-decoration: none;
      color: $black-clr;
      @include hover {
        color: $accent-clr;
      }
      i {
        font-size: 8px;
        color: $accent-clr;
        transform: rotate(180deg);
        transition: transform 0.25s;
      }
      &[aria-expanded="false"] {
        i {
          transform: rotate(0);
        }
      }
    }
  }
  .product-description__body {
    padding: 10px 0 0;
  }
}

.select2-results__options {
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
  // box-shadow: 5px 9px 30px rgba(69, 69, 69, 0.12);
  overflow: hidden;
  // li {
  // 	&:last-of-type {
  // 		border-bottom-left-radius: 8px;
  // 		border-bottom-right-radius: 8px;
  // 	}
  // }
}

.product-description__text {
  font-size: 18px;
  line-height: 27px;
  color: #565656;
}

.characteristics-item {
  display: flex;
  margin-bottom: 1rem;
  * {
    font-weight: 400;
    margin: 0;
  }
  .characteristics-label {
    flex: 0 0 180px;
  }
}

/* Colors */

.card-colors {
  .card-colors__list {
    list-style: none;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    padding: 0 30px 0;
    @media (max-width: 1200px) {
      padding: 0 17px 0;
    }
    @media (max-width: 1024px) {
      padding: 0 5px 0;
    }
  }
  .card-colors__item {
    margin-bottom: 24px;
    margin-right: 10px;
    width: 38px;
    height: 38px;
    border-radius: 4px;
  }
  .card-colors__option-color {
    &.color-active {
      &::after {
        content: "";
        position: absolute;
        border: 2px solid $accent-clr;
        width: 46px;
        height: 46px;
        left: -4px;
        top: -4px;
        border-radius: 8px;
      }
    }
    cursor: pointer;
    position: relative;
    display: block;
    border-radius: 4px;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 4px;
      border: 1px solid $grey-superlight-clr;
    }
    img {
      width: 38px;
      height: 38px;
      object-fit: cover;
      border-radius: 4px;
    }
    @include hover {
      .card-colors__option-tooltip {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  .card-colors__option-tooltip {
    position: absolute;
    top: -100%;
    font-size: 10px;
    color: $black-lighter-clr;
    width: auto;
    min-width: 38px;
    max-width: 10rem;
    text-align: center;
    background-color: $grey-superlight-clr;
    display: block;
    padding: 2px 5px;
    opacity: 0;
    visibility: hidden;
    border-radius: 4px;
    word-wrap: break-word;
    z-index: 2;
  }
}

@media (max-width: 1439px) {
  .product-card-wrapper {
    .product-card__left,
    .product-card__right {
      flex: 0 0 50%;
      max-width: 50%;
      margin-bottom: 65px;
    }
  }
}
@media (max-width: 1200px) {
  .product-slider-wrapper {
    .product-slider__thumbs {
      .slick-list {
        margin: 0 -4px;
      }
    }
    .product-slider__thumbs-item {
      margin: 0 4px;
      &::before {
        border-width: 2px;
      }
    }
  }
  .product-card__about {
    .product-card__about-header {
      padding: 26px 16px 0;
    }
    .product-card__selects {
      padding: 0 16px 17px;
      .select2-container
        .select2-selection--single
        .select2-selection__rendered
        .card-select-list {
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        padding: 10px 0 16px;
        .state-title {
          display: block;
          font-size: 1rem;
          line-height: 27px;
          margin-bottom: 5px;
        }
        .state-text {
          width: 300px;
          @media (max-width: 575px) {
            width: 200px;
          }
          @media (max-width: 375px) {
            width: 160px;
          }
        }
      }
      .select2-container .select2-selection--single {
        height: 91px;
        margin-bottom: 8px;
      }
    }
  }
  .product-card__description {
    padding: 23px 16px 18px;
  }
}

@media (max-width: 1024px) {
  .product-card-wrapper {
    .product-card__row {
      margin: 0 -10px;
    }
    .product-card__left,
    .product-card__right {
      padding: 0 10px;
    }
    .product-card__left {
      height: 100%;
      position: sticky;
      top: 80px;
    }
  }
  .product-description__text {
    font-size: 16px;
    line-height: 26px;
  }
  .product-card__title {
    font-size: 36px;
    line-height: 48px;
  }
}

@media (max-width: 991px) {
  .characteristics-item {
    flex-direction: column;
    .characteristics-label {
      flex: 0;
    }
    .product-description__text {
      line-height: 36px;
    }
  }
}

@media (max-width: 1024px) {
  .product-card-wrapper {
    .product-card__left,
    .product-card__right {
      flex: 0 0 100%;
      max-width: 100%;
    }
    .product-card__left {
      position: static;
      top: 0;
      margin-bottom: 40px;
    }
    .product-card__right {
      margin-bottom: 0;
    }
    .product-card__row {
      background-color: $white-clr;
      box-shadow: 5px 9px 30px rgba(175, 175, 175, 0.12);
      border-radius: 4px;
      margin: 0;
    }
    .product-card__about-header {
      padding: 26px 15px 0;
      width: 100%;
    }
    .product-card__id {
      display: flex;
      width: 100%;
      flex-wrap: wrap;
      .product-availability {
        flex: 0 0 100%;
      }
    }
    .id-text {
      font-size: 1rem;
      line-height: 36px;
      color: $grey-lighter-clr;
      margin-right: 20px;
      margin-bottom: 0;
    }
  }
  .product-card__title {
    max-width: 100%;
  }
  .product-slider-wrapper {
    .product-slider__thumbs {
      padding: 0 4px 0 4px;
    }
    .product-slider__thumbs-item {
      &::before {
        border-width: 1px;
      }
      &.slick-current {
        &::before {
          border-width: 2px;
        }
      }
    }
    .product-slider__thumbs-item {
      &::before {
        opacity: 1;
        visibility: visible;
        border-color: $grey-superlight-clr;
      }
      &.slick-current {
        &::before {
          border-color: $accent-clr;
        }
      }
    }
    .product-slider__item {
      &::after {
        padding-top: 35%;
      }
      &::before {
        padding-bottom: 35%;
      }
    }
  }
  .product-card__about {
    box-shadow: none;
    .product-card__selects {
      padding: 0 6px 17px;
    }
  }
  .characteristics-item {
    flex-direction: row;
  }
  .characteristics-item {
    .characteristics-label {
      flex: 0 0 50%;
    }
  }
  .product-card__description {
    border: none;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: -10px;
      width: calc(100% + 20px);
      height: 1px;
      background-color: $grey-superlight-clr;
    }
    &:last-of-type {
      &::after {
        display: none;
      }
    }
  }
  .product-card__selects {
    position: relative;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: -10px;
      width: calc(100% + 20px);
      height: 1px;
      background-color: $grey-superlight-clr;
    }
  }
  .similar {
    padding-top: 65px;
  }
}

@media (max-width: 575px) {
  .product-slider-wrapper {
    .product-slider__item {
      &::after {
        padding-top: 50%;
      }
      &::before {
        padding-bottom: 50%;
      }
    }
  }
  .characteristics-item {
    .characteristics-label {
      flex: 0 0 40%;
    }
  }
  .similar {
    padding-top: 30px;
  }
}

@media (max-width: 480px) {
  .characteristics-item {
    flex-direction: column;
  }
}
