.orders-accordion {
  // background-color: $white-clr;
  border-radius: 4px;
}

.orders-wrapper {
  overflow: hidden;
}

.orders {
  // background-color: $white-clr;
}

.accordion-card {
  border: 1px solid $grey-superlight-clr;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.03);
  border-radius: 4px;
  margin-bottom: 8px;
  // overflow: hidden;
  .accordion-card__header-info {
    span {
      font-size: 14px;
      color: $grey-lighter-clr;
      display: inline-block;
      margin-right: 24px;
    }
  }
  .accordion-card__id {
    font-weight: bold;
    font-family: $font-family-proba;
    font-size: 18px;
    line-height: 1;
    margin-bottom: 10px;
  }
  &[data-order-status="in-process"] {
    .accordion-card__id {
      color: $process;
    }
    .accordion-card__status {
      .accordion-card__status-icon {
        &::before {
          content: "\e90b";
          color: $process;
        }
        @include hover {
          border-color: $process;
          background-color: $process;
          &::before {
            color: $white-clr;
          }
        }
      }
      &.active {
        .accordion-card__status-icon {
          border-color: $process;
          background-color: $process;
          &::before {
            color: $white-clr;
          }
        }
      }
    }
  }
  &[data-order-status="reserve"] {
    .accordion-card__id {
      color: $reserve;
    }
    .accordion-card__status {
      .accordion-card__status-icon {
        &::before {
          content: "\e914";
          color: $reserve;
        }
        @include hover {
          border-color: $reserve;
          background-color: $reserve;
          &::before {
            color: $white-clr;
          }
        }
      }
      &.active {
        .accordion-card__status-icon {
          border-color: $reserve;
          background-color: $reserve;
          &::before {
            color: $white-clr;
          }
        }
      }
    }
  }
  &[data-order-status="shipped"] {
    .accordion-card__id {
      color: $shipped;
    }
    .accordion-card__status {
      .accordion-card__status-icon {
        &::before {
          content: "\e90f";
          color: $shipped;
        }
        @include hover {
          border-color: $shipped;
          background-color: $shipped;
          &::before {
            color: $white-clr;
          }
        }
      }
      &.active {
        .accordion-card__status-icon {
          border-color: $shipped;
          background-color: $shipped;
          &::before {
            color: $white-clr;
          }
        }
      }
    }
  }
  &[data-order-status="decline"] {
    .accordion-card__id {
      color: $decline;
    }
    .accordion-card__status {
      .accordion-card__status-icon {
        &::before {
          content: "\e90a";
          color: $decline;
          font-size: 11px;
        }
        @include hover {
          border-color: $decline;
          background-color: $decline;
          &::before {
            color: $white-clr;
          }
        }
      }
      &.active {
        .accordion-card__status-icon {
          border-color: $decline;
          background-color: $decline;
          &::before {
            color: $white-clr;
          }
        }
      }
    }
  }
  &[data-order-status="warehouse-reserve"] {
    .accordion-card__id {
      color: $warehouse;
    }
    .accordion-card__status {
      .accordion-card__status-icon {
        &::before {
          content: "\e909";
          color: $warehouse;
        }
        @include hover {
          border-color: $warehouse;
          background-color: $warehouse;
          &::before {
            color: $white-clr;
          }
        }
      }
      &.active {
        .accordion-card__status-icon {
          border-color: $warehouse;
          background-color: $warehouse;
          &::before {
            color: $white-clr;
          }
        }
      }
    }
  }
  &[data-order-status="warehouse-shipped"] {
    .accordion-card__id {
      color: $warehouse-shipped;
    }
    .accordion-card__status {
      .accordion-card__status-icon {
        &::before {
          content: "\e908";
          color: $warehouse-shipped;
        }
        @include hover {
          border-color: $warehouse-shipped;
          background-color: $warehouse-shipped;
          &::before {
            color: $white-clr;
          }
        }
      }
      &.active {
        .accordion-card__status-icon {
          border-color: $warehouse-shipped;
          background-color: $warehouse-shipped;
          &::before {
            color: $white-clr;
          }
        }
      }
    }
  }
}

.accordion-card__header {
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
  padding: 16px 0;
  background-color: $white-clr;
}

.accordion-card__btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  padding-right: 24px;
  padding-left: 61px;
  position: relative;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-radius: 4px;
  background-color: $white-clr;
  column-gap: 15px;
  border: none;
  appearance: none;
  -webkit-appearance: none;
  .accordion-card__info {
    // padding-top: 16px;
    // padding-bottom: 16px;
  }
  .accordion-card__repeat {
    // padding-top: 16px;
    // padding-bottom: 16px;
  }
  &::before {
    content: "\e912";
    position: absolute;
    left: 24px;
    top: 50%;
    transform: translateY(-50%) rotate(180deg);
    width: 12px;
    height: 8px;
    font-family: "icomoon" !important;
    font-size: 10px;
    color: $grey-lighter-clr;
    transform-origin: center center;
    transition: 0.25s;
  }
  &.collapsed {
    &::before {
      transform: translateY(-50%) rotate(0);
    }
  }
  @media (max-width: 1300px) {
    justify-content: flex-start;
    .accordion-card__repeat,
    .accordion-card__status {
      margin-left: auto;
    }
    .admin-products__cart-btn,
    .accordion-card__price span {
      font-size: 14px;
    }
  }
  @media (max-width: 1200px) {
    .accordion-card__prices {
      margin-left: auto;
    }
    .accordion-card__photos {
      .accordion-card__thumb {
        width: 35px;
        height: 35px;
        img {
          width: 35px;
          height: 35px;
        }
      }
    }
  }
  @media (max-width: 1024px) {
    display: grid;
    grid-template-columns: 1fr 2.5fr 1fr 0.5fr;
    row-gap: 16px;
    padding-left: 16px;
    &::before {
      top: 19px;
      left: 16px;
    }
    &.collapsed {
      transform: translateY(0) rotate(0);
    }
    .accordion-card__id {
      padding-left: 29px;
    }
    .accordion-card__repeat {
      grid-column-start: 1;
      grid-column-end: 5;
      grid-row-start: 2;
      grid-row-end: 2;
      width: 100%;
      display: flex;
      justify-content: center;
      padding-top: 14px;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: -16px;
        height: 1px;
        width: calc(100% + 40px);
        background-color: $grey-superlight-clr;
      }
      .admin-products__cart-btn {
        margin: 0;
      }
    }
    .accordion-card__status {
      .accordion-card__status-tooltip {
        padding: 14px;
        left: calc(-50% - 10px);
        bottom: calc(100% + 10px);
        &::after {
          transform: translateX(0);
          left: auto;
          right: 33px;
        }
        &::before {
          left: auto;
          right: 32px;
        }
      }
    }
  }
  @media (max-width: 768px) {
    grid-template-columns: repeat(2, 50%);
    row-gap: 10px;
    padding-right: 1rem;
    &::before {
      top: 8px;
    }
    .accordion-card__info {
      order: 1;
      grid-column-start: 1;
      grid-column-end: 1;
      grid-row-start: 1;
      grid-row-end: 1;
    }
    .accordion-card__status {
      order: 2;
      grid-column-start: 2;
      grid-column-end: 2;
      grid-row-start: 1;
      grid-row-end: 1;
      margin-right: 1rem;
    }
    .accordion-card__repeat {
      order: 3;
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 3;
      grid-row-end: 3;
      padding-bottom: 16px;
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: -16px;
        height: 1px;
        width: calc(100% + 40px);
        background-color: $grey-superlight-clr;
      }
    }
    .accordion-card__photos {
      order: 4;
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 4;
      grid-row-end: 4;
      justify-content: flex-start;
      row-gap: 5px;
      .accordion-card__thumb {
        width: 50px;
        height: 50px;
        img {
          width: 50px;
          height: 50px;
        }
      }
    }
    .accordion-card__prices {
      order: 5;
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 5;
      grid-row-end: 5;
      width: 100%;
      margin-left: 0;
      display: flex;
      label {
        margin-bottom: 0;
      }
    }
  }
  @media (max-width: 480px) {
    .accordion-card__prices {
      display: flex;
      flex-wrap: wrap;
    }
  }
}

.accordion-card__photos {
  display: flex;
  column-gap: 8px;
  row-gap: 8px;
  flex-wrap: wrap;
  max-width: 400px;
  justify-content: center;
  .accordion-card__thumb {
    display: block;
    width: 50px;
    height: 50px;
    overflow: hidden;
    border: 1px solid transparent;
    border-radius: 4px;
    img {
      width: 50px;
      height: 50px;
      object-fit: cover;
    }
    span {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 4px;
      border: 1px solid $grey-superlight-clr;
      font-weight: bold;
      font-size: 14px;
    }
  }
}

.accordion-card__prices {
  column-gap: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: 270px;
  width: 100%;
  text-align: right;
  &.manager-card__prices {
    grid-template-columns: 1fr;
    column-gap: 0;
  }
  @media (max-width: 1300px) {
    grid-template-columns: 1fr;
    margin-left: auto;
  }
  @media (max-width: 768px) {
    padding-right: 1rem;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .accordion-card__price-label {
    order: 1;
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 1;
    justify-self: flex-end;
    @media (max-width: 768px) {
      grid-column-start: 1;
      grid-column-end: 1;
      grid-row-start: 1;
      grid-row-end: 1;
    }
  }
  .accordion-card__price-retail {
    order: 2;
    @media (max-width: 1300px) {
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 2;
      grid-row-end: 2;
      justify-self: flex-end;
    }
    @media (max-width: 768px) {
      grid-column-start: 2;
      grid-column-end: 2;
      grid-row-start: 1;
      grid-row-end: 1;
    }
  }
  .accordion-card__sum {
    order: 3;
    @media (max-width: 1300px) {
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 3;
      grid-row-end: 3;
      justify-self: flex-end;
    }
    @media (max-width: 768px) {
      grid-column-start: 3;
      grid-column-end: 3;
      grid-row-start: 1;
      grid-row-end: 1;
    }
  }
}

.accordion-card__price {
  position: relative;
  span,
  &.accordion-card__price-label {
    color: $grey-lighter-clr;
  }
  span {
    font-size: 18px;
    display: inline-block;
    line-height: 22px;
  }
  &.accordion-card__price-label {
    font-size: 14px;
    line-height: 14px;
    margin-bottom: 0;
    display: flex;
    align-items: center;
  }
  .text-bold {
    color: $black-clr;
    margin-left: 10px;
  }
}

.accordion-card__status {
  position: relative;
  // padding-top: 16px;
  &.active {
    .accordion-card__status-tooltip {
      opacity: 1;
      visibility: visible;
      transform: translateX(-50%) translateY(0);
    }
  }
  .accordion-card__status-icon {
    width: 44px;
    height: 44px;
    border: 1px solid $grey-superlight-clr;
    border-radius: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.25s;
    cursor: pointer;
    @include hover {
      + .accordion-card__status-tooltip {
        opacity: 1;
        visibility: visible;
        transform: translateX(-50%) translateY(0);
      }
    }
    &::before {
      font-family: "icomoon" !important;
      width: 22px;
      height: 22px;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: 0.35s;
    }
  }
  .accordion-card__status-tooltip {
    width: 190px;
    position: absolute;
    bottom: calc(100% + 15px);
    left: 50%;
    transform: translateX(-50%) translateY(10px);
    background-color: $white-clr;
    border: 1px solid $grey-superlight-clr;
    padding: 14px 16px 17px;
    border-radius: 4px;
    pointer-events: none;
    opacity: 0;
    visibility: hidden;
    transition: 0.25s;
    &::before {
      content: "";
      bottom: -20px;
      display: block;
      height: 20px;
      left: 86px;
      position: absolute;
      border-color: $grey-superlight-clr transparent transparent transparent;
      border-style: solid;
      border-width: 8px;
    }
    &::after {
      border-left: solid transparent 7px;
      border-right: solid transparent 7px;
      border-top: solid $white-clr 7px;
      bottom: -7px;
      content: " ";
      height: 0;
      left: 50%;
      transform: translateX(-50%);
      // margin-left: -13px;
      position: absolute;
      width: 0;
    }
    span {
      font-size: 14px;
      line-height: 21px;
    }
  }
}

.accordion-card__body {
  background-color: $white-clr;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.accordion-card__body-inner-item {
  display: grid;
  grid-template-columns: 73px 1fr 1fr;
  padding: 0 24px;
  border-top: 1px solid $grey-superlight-clr;
  column-gap: 25px;
  .admin-products__item-img {
    grid-area: initial;
  }
  @media (max-width: 1024px) {
    column-gap: 10px;
  }
  @media (max-width: 768px) {
    grid-template-columns: 64px 1fr 1fr;
    padding: 0 16px;
    .admin-products__item-img {
      grid-column-start: 1;
      grid-column-end: 1;
      grid-row-start: 2;
      grid-row-end: 2;
      width: 64px;
      height: 64px;
    }
    .accordion-card__body-product {
      grid-column-start: 1;
      grid-column-end: 4;
      grid-row-start: 1;
      grid-row-end: 1;
      width: 100%;
      .accordion-card__body-product-name {
        padding-bottom: 0;
      }
    }
    .accordion-card__body-prices {
      grid-column-start: 2;
      grid-column-end: 4;
      grid-row-start: 2;
      grid-row-end: 2;
      display: flex;
      justify-content: flex-end;
      width: 100%;
      column-gap: 20px;
    }
  }
}

.accordion-card__body-product {
  display: flex;
  align-items: center;
}

.accordion-card__body-left,
.accordion-card__body-right {
  display: flex;
  align-items: center;
}

.accordion-card__body-left {
  column-gap: 16px;
}

.accordion-card__body-prices {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 40px;
  font-size: 14px;
  padding: 14px 0;
  padding-right: 16px;
  margin-left: auto;
  @media (max-width: 1024px) {
    padding-right: 0;
  }
  .accordion-card__count,
  .accordion-card__retail {
    span {
      display: inline-block;
      &:not(.text-dark) {
        color: #565656;
        margin-right: 5px;
      }
    }
    span.text-dark {
      // line-height: 1.3;
    }
  }
  .accordion-card__retail {
    span {
      &:not(.text-dark) {
        color: $grey-lighter-clr;
      }
    }
  }
}

.accordion-card__body-product-name {
  font-size: 18px;
  line-height: 24px;
  max-width: 320px;
  display: block;
  padding: 14px 0;
  text-decoration: none;
  transition: 0.25s;
  display: block;
  color: $black-clr;
  span {
    margin-left: 5px;
  }
  @include hover {
    color: $accent-clr !important;
    text-decoration: none;
  }
}

.accordion-card__body-footer {
  padding: 16px 24px 17px 35px;
  border-top: 1px solid $grey-superlight-clr;
  display: flex;
  justify-content: space-between;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  @media (max-width: 768px) {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-left: 16px;
    padding-right: 16px;
    .accordion-card__body-right {
      width: 100%;
    }
    .accordion-card__body-prices {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      width: 100%;
      padding-bottom: 0;
      .accordion-card__retail {
        display: flex;
        width: 100%;
        justify-content: space-between;
        margin-bottom: 15px;
        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }
  }
}

@media (max-width: 1024px) {
  .orders-wrapper {
    .admin-catalog__header-sorting {
      width: 100%;
    }
    .admin-catalog__sorting {
      justify-content: space-between;
    }
  }
}

@media (max-width: 768px) {
  .orders-wrapper {
    .admin-catalog__header-prices {
      margin-bottom: 22px;
    }
  }
}

@media (max-width: 575px) {
  .orders-wrapper {
    .admin-catalog__header-prices,
    .admin-catalog__sorting-select {
      width: 100%;
    }
    .admin-catalog__header-prices {
      background-color: $white-clr;
      margin-right: 0 !important;
      padding: 21px 16px;
    }
    .admin-catalog__header {
      .admin-catalog__sorting-select {
        width: 100%;
        margin-left: 0 !important;
      }
    }
  }
}
